'use client'

import { useState } from 'react'
import { 
  CubeIcon, 
  ArrowTrendingUpIcon, 
  ExclamationTriangleIcon,
  BuildingStorefrontIcon,
  TagIcon,
  Bars3Icon,
  XMarkIcon
} from '@heroicons/react/24/outline'

interface InventoryItem {
  id: string
  name: string
  category: string
  quantity: number
  unit: string
  minQuantity: number
  location: string
  price: number
  supplier: string
  lastRestocked: string
}

export default function LagerPage() {
  const [isSidebarOpen, setIsSidebarOpen] = useState(false)
  const [showAddModal, setShowAddModal] = useState(false)
  const [showEditModal, setShowEditModal] = useState(false)
  const [editingItem, setEditingItem] = useState<InventoryItem | null>(null)
  const [newItem, setNewItem] = useState<Partial<InventoryItem>>({
    name: '',
    category: '',
    quantity: 0,
    unit: '',
    minQuantity: 0,
    location: '',
    price: 0,
    supplier: '',
    lastRestocked: new Date().toISOString().split('T')[0]
  })

  const [inventory, setInventory] = useState<InventoryItem[]>([
    {
      id: '1',
      name: 'Torv',
      category: 'Taktekking',
      quantity: 150,
      unit: 'pakker',
      minQuantity: 50,
      location: 'Lager A',
      price: 250,
      supplier: 'Torv AS',
      lastRestocked: '2024-03-01'
    },
    {
      id: '2',
      name: 'Skigard',
      category: 'Gjerder',
      quantity: 200,
      unit: 'meter',
      minQuantity: 100,
      location: 'Lager B',
      price: 150,
      supplier: 'Gjerde & Co',
      lastRestocked: '2024-03-10'
    },
    {
      id: '3',
      name: 'Kjøreport',
      category: 'Portar',
      quantity: 5,
      unit: 'stk',
      minQuantity: 2,
      location: 'Lager C',
      price: 15000,
      supplier: 'Port & Gjerde',
      lastRestocked: '2024-02-15'
    },
    {
      id: '4',
      name: 'Takspiker',
      category: 'Fester',
      quantity: 1000,
      unit: 'stk',
      minQuantity: 500,
      location: 'Lager A',
      price: 2.5,
      supplier: 'Byggevare AS',
      lastRestocked: '2024-03-05'
    }
  ])

  const handleAddItem = (e: React.FormEvent) => {
    e.preventDefault()
    if (!newItem.name || !newItem.category) return

    const item: InventoryItem = {
      ...newItem as InventoryItem,
      id: Date.now().toString()
    }

    setInventory([...inventory, item])
    setNewItem({
      name: '',
      category: '',
      quantity: 0,
      unit: '',
      minQuantity: 0,
      location: '',
      price: 0,
      supplier: '',
      lastRestocked: new Date().toISOString().split('T')[0]
    })
    setShowAddModal(false)
  }

  const handleEditItem = (item: InventoryItem) => {
    setEditingItem(item)
    setShowEditModal(true)
  }

  const handleUpdateItem = (e: React.FormEvent) => {
    e.preventDefault()
    if (!editingItem) return

    setInventory(inventory.map(item => 
      item.id === editingItem.id ? editingItem : item
    ))
    setShowEditModal(false)
    setEditingItem(null)
  }

  const handleDeleteItem = (id: string) => {
    if (window.confirm('Er du sikker på at du vil slette dette varen?')) {
      setInventory(inventory.filter(item => item.id !== id))
    }
  }

  const sidebarItems = [
    { name: 'Alle varer', icon: CubeIcon, count: inventory.length },
    { name: 'Lav beholdning', icon: ExclamationTriangleIcon, count: inventory.filter(item => item.quantity <= item.minQuantity).length },
    { name: 'Kategorier', icon: TagIcon, count: new Set(inventory.map(item => item.category)).size },
    { name: 'Leverandører', icon: BuildingStorefrontIcon, count: new Set(inventory.map(item => item.supplier)).size },
    { name: 'Beholdningstrender', icon: ArrowTrendingUpIcon },
  ]

  return (
    <div className="min-h-screen bg-gray-900 p-8">
      {/* Hamburger Menu Button */}
      <button
        onClick={() => setIsSidebarOpen(!isSidebarOpen)}
        className="lg:hidden fixed top-4 left-4 z-50 p-2 rounded-lg bg-gray-800 text-white hover:bg-gray-700"
      >
        {isSidebarOpen ? (
          <XMarkIcon className="w-6 h-6" />
        ) : (
          <Bars3Icon className="w-6 h-6" />
        )}
      </button>

      <div className="max-w-7xl mx-auto">
        <div className="flex justify-between items-center mb-12">
          <div>
            <h1 className="text-4xl font-bold text-white">Lager</h1>
            <p className="text-gray-400 mt-2">Administrer varelager</p>
          </div>
          <button
            onClick={() => setShowAddModal(true)}
            className="bg-blue-600 hover:bg-blue-700 text-white px-6 py-3 rounded-full flex items-center gap-2 shadow-sm transition-all duration-200"
          >
            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
            </svg>
            <span>Ny vare</span>
          </button>
        </div>

        <div className="flex gap-8">
          {/* Sidebar */}
          <div className={`
            fixed lg:static inset-y-0 left-0 z-40
            transform ${isSidebarOpen ? 'translate-x-0' : '-translate-x-full'}
            lg:translate-x-0 transition-transform duration-300 ease-in-out
            w-64 bg-gray-900 border-r border-gray-800 p-6
          `}>
            <div className="space-y-2">
              {sidebarItems.map((item) => (
                <button
                  key={item.name}
                  className="w-full flex items-center px-4 py-3 text-sm font-medium rounded-xl
                    text-gray-400 hover:bg-gray-800 hover:text-white
                    transition-all duration-200 group"
                >
                  <item.icon className="w-5 h-5 mr-3 group-hover:text-blue-500 transition-colors" />
                  <span className="flex-1 text-left">{item.name}</span>
                  {item.count !== undefined && (
                    <span className="bg-gray-800 text-gray-400 group-hover:bg-gray-700 
                      group-hover:text-white px-2.5 py-1 rounded-lg text-xs transition-colors">
                      {item.count}
                    </span>
                  )}
                </button>
              ))}
            </div>
          </div>

          {/* Overlay når sidebar er åpen på mobil */}
          {isSidebarOpen && (
            <div 
              className="fixed inset-0 bg-black/50 backdrop-blur-sm z-30 lg:hidden"
              onClick={() => setIsSidebarOpen(false)}
            />
          )}

          {/* Hovedinnhold */}
          <div className="flex-1">
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {inventory.map(item => (
                <div key={item.id} className="bg-gray-800 rounded-2xl p-6 border border-gray-700">
                  <div className="flex justify-between items-start mb-4">
                    <h3 className="text-xl font-semibold text-white">{item.name}</h3>
                    <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                      item.quantity <= item.minQuantity
                        ? 'bg-red-900/50 text-red-400'
                        : 'bg-green-900/50 text-green-400'
                    }`}>
                      {item.quantity} {item.unit}
                    </span>
                  </div>
                  <div className="space-y-2 mb-4">
                    <p className="text-sm text-gray-400">Kategori: <span className="text-white">{item.category}</span></p>
                    <p className="text-sm text-gray-400">Lokasjon: <span className="text-white">{item.location}</span></p>
                    <p className="text-sm text-gray-400">Leverandør: <span className="text-white">{item.supplier}</span></p>
                    <p className="text-sm text-gray-400">Pris: <span className="text-white">{item.price.toLocaleString('nb-NO')} kr</span></p>
                    <p className="text-sm text-gray-400">Min. antall: <span className="text-white">{item.minQuantity} {item.unit}</span></p>
                    <p className="text-sm text-gray-400">Sist påfylt: <span className="text-white">{new Date(item.lastRestocked).toLocaleDateString('nb-NO')}</span></p>
                  </div>
                  <div className="flex flex-wrap gap-2">
                    <button
                      onClick={() => handleEditItem(item)}
                      className="px-3 py-1 rounded-lg text-sm bg-blue-900/50 text-blue-400 hover:bg-blue-900/70"
                    >
                      Endre
                    </button>
                    <button
                      onClick={() => handleDeleteItem(item.id)}
                      className="px-3 py-1 rounded-lg text-sm bg-red-900/50 text-red-400 hover:bg-red-900/70"
                    >
                      Slett
                    </button>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* Add Modal */}
        {showAddModal && (
          <div className="fixed inset-0 bg-black/50 flex items-center justify-center">
            <div className="bg-gray-800 rounded-2xl p-8 max-w-md w-full">
              <h2 className="text-2xl font-bold text-white mb-6">Ny vare</h2>
              <form onSubmit={handleAddItem} className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-400 mb-1">Varenavn</label>
                  <input
                    type="text"
                    value={newItem.name}
                    onChange={(e) => setNewItem({ ...newItem, name: e.target.value })}
                    className="w-full bg-gray-700 border border-gray-600 rounded-lg px-4 py-2 text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-400 mb-1">Kategori</label>
                  <input
                    type="text"
                    value={newItem.category}
                    onChange={(e) => setNewItem({ ...newItem, category: e.target.value })}
                    className="w-full bg-gray-700 border border-gray-600 rounded-lg px-4 py-2 text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-400 mb-1">Antall</label>
                  <input
                    type="number"
                    value={newItem.quantity}
                    onChange={(e) => setNewItem({ ...newItem, quantity: Number(e.target.value) })}
                    className="w-full bg-gray-700 border border-gray-600 rounded-lg px-4 py-2 text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-400 mb-1">Enhet</label>
                  <input
                    type="text"
                    value={newItem.unit}
                    onChange={(e) => setNewItem({ ...newItem, unit: e.target.value })}
                    className="w-full bg-gray-700 border border-gray-600 rounded-lg px-4 py-2 text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-400 mb-1">Min. antall</label>
                  <input
                    type="number"
                    value={newItem.minQuantity}
                    onChange={(e) => setNewItem({ ...newItem, minQuantity: Number(e.target.value) })}
                    className="w-full bg-gray-700 border border-gray-600 rounded-lg px-4 py-2 text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-400 mb-1">Lokasjon</label>
                  <input
                    type="text"
                    value={newItem.location}
                    onChange={(e) => setNewItem({ ...newItem, location: e.target.value })}
                    className="w-full bg-gray-700 border border-gray-600 rounded-lg px-4 py-2 text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-400 mb-1">Pris</label>
                  <input
                    type="number"
                    value={newItem.price}
                    onChange={(e) => setNewItem({ ...newItem, price: Number(e.target.value) })}
                    className="w-full bg-gray-700 border border-gray-600 rounded-lg px-4 py-2 text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-400 mb-1">Leverandør</label>
                  <input
                    type="text"
                    value={newItem.supplier}
                    onChange={(e) => setNewItem({ ...newItem, supplier: e.target.value })}
                    className="w-full bg-gray-700 border border-gray-600 rounded-lg px-4 py-2 text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-400 mb-1">Sist påfylt</label>
                  <input
                    type="date"
                    value={newItem.lastRestocked}
                    onChange={(e) => setNewItem({ ...newItem, lastRestocked: e.target.value })}
                    className="w-full bg-gray-700 border border-gray-600 rounded-lg px-4 py-2 text-white focus:outline-none focus:ring-2 focus:ring-blue-500"
                    required
                  />
                </div>
                <div>
                  <button type="submit" className="w-full bg-blue-600 hover:bg-blue-700 text-white px-6 py-3 rounded-full">
                    Legg til vare
                  </button>
                </div>
              </form>
            </div>
          </div>
        )}
      </div>
    </div>
  )
} 